<script>
import { deepClone } from '@/utils/index'
import render from '@/components/render/render.js'
const ruleTrigger = {
  'el-input': 'blur',
  'el-input-number': 'blur',
  'el-select': 'change',
  'el-radio-group': 'change',
  'el-checkbox-group': 'change',
  'el-cascader': 'change',
  'el-time-picker': 'change',
  'el-date-picker': 'change',
  'el-rate': 'change'
}
const layouts = {
  colFormItem(h, scheme) {
    const config = scheme.__config__
    const listeners = buildListeners.call(this, scheme)
    let labelWidth = config.labelWidth ? `${config.labelWidth}px` : null
    if (config.showLabel === false) labelWidth = '0'
    return (
      <el-col span={config.span}>
        <el-form-item label-width={labelWidth} prop={scheme.__vModel__}
          label={config.showLabel ? config.label : ''}>
          <render conf={scheme} {...{ on: listeners }} />
        </el-form-item>
      </el-col>
    )
  },
  rowFormItem(h, scheme) {
    let child = renderChildren.apply(this, arguments)
    if (scheme.type === 'flex') {
      child = <el-row type={scheme.type} justify={scheme.justify} align={scheme.align}>
              {child}
            </el-row>
    }
    return (
      <el-col span={scheme.span}>
        <el-row gutter={scheme.gutter}>
          {child}
        </el-row>
      </el-col>
    )
  }
}
function renderFrom(h) {
  const { formConfCopy } = this
  return (
    <el-row gutter={formConfCopy.gutter}>
      <el-form
        size={formConfCopy.size}
        label-position={formConfCopy.labelPosition}
        disabled={formConfCopy.disabled}
        label-width={`${formConfCopy.labelWidth}px`}
        ref={formConfCopy.formRef}
        // model https://github.com/vuejs/jsx/issues/49#issuecomment-472013664
        props={{ model: this[formConfCopy.formModel] }}
        rules={this[formConfCopy.formRules]}
      >
        {renderFormItem.call(this, h, formConfCopy.fields)}
      </el-form>
    </el-row>
  )
}
// {formConfCopy.formBtns && formBtns.call(this, h)}
function formBtns(h) {
  return <el-col>
    <el-form-item size="large">
      <el-button type="primary" onClick={this.submitForm}></el-button>
      <el-button onClick={this.resetForm}></el-button>
    </el-form-item>
  </el-col>
}
function renderFormItem(h, elementList) {
  return elementList.map(scheme => {
    const config = scheme.__config__
    const layout = layouts[config.layout]
    if (layout) {
      return layout.call(this, h, scheme)
    }
    throw new Error(`${config.layout}layout`)
  })
}
function renderChildren(h, scheme) {
  const config = scheme.__config__
  if (!Array.isArray(config.children)) return null
  return renderFormItem.call(this, h, config.children)
}
function setValue(event, config, scheme) {
  this.$set(config, 'defaultValue', event)
  this.$set(this[this.formConf.formModel], scheme.__vModel__, event)
}
function buildListeners(scheme) {
  const config = scheme.__config__
  const methods = this.formConf.__methods__ || {}
  const listeners = {}
  // __methods__thisevent
  Object.keys(methods).forEach(key => {
    listeners[key] = event => methods[key].call(this, event)
  })
  //  render.js  vModel $emit('input', val)
  listeners.input = event => {
    setValue.call(this, event, config, scheme)
    this.doSomethingInput(
      event,
      scheme.__vModel__,
      this[this.formConf.formModel],
      scheme
    );
  }
  listeners.change = event => {
    setValue.call(this, event, config, scheme)
    this.doSomethingChange(
      event,
      scheme.__vModel__,
      this[this.formConf.formModel],
      scheme
    );
  }
  listeners.click1 = event => {
    // setValue.call(this, event, config, scheme)
    this.doSomething2(
      event,
      scheme.__vModel__,
      this[this.formConf.formModel],
      scheme
    );
  }
  listeners.otherClick = event => {
    // setValue.call(this, event, config, scheme)
    this.doSomethingother(
      event,
      scheme.__vModel__,
      this[this.formConf.formModel],
      scheme
    );
  }
  return listeners
}
export default {
  components: {
    render
  },
  props: {
    formConf: {
      type: Object,
      required: true
    }
  },
  data() {
    const data = {
      formConfCopy: deepClone(this.formConf),
      [this.formConf.formModel]: {},
      [this.formConf.formRules]: {}
    }
    this.initFormData(data.formConfCopy.fields, data[this.formConf.formModel])
    this.buildRules(data.formConfCopy.fields, data[this.formConf.formRules])
    return data
  },
  methods: {
    DateDiff(sDate1, sDate2){    
          var  aDate,  oDate1,  oDate2,  iDays  
          aDate   =  sDate1.split("-")  
          oDate1 = new Date(aDate[0] , aDate[1] ,aDate[2])    //12-18-2006  
          aDate   =  sDate2.split("-")  
          oDate2 = new Date(aDate[0] , aDate[1] , aDate[2])  
          iDays   =  parseInt(Math.abs(oDate1  -  oDate2)  /  1000  /  60  /  60  /24)    //  
        return  iDays
    },
    doSomethingInput(data, model, value,codetype) {
      this.$emit("funcInput", data, model, value ,codetype);
    },
    doSomethingChange(data, model, value,codetype) {
      this.$emit("funcChange", data, model, value ,codetype);
    },
    doSomething2(data, model, value,codetype) {
      if(codetype.__config__.tagIcon == 'Customcodetable') {
        this.$emit("func", data, model, value ,codetype, 1);
      } else if(codetype.__config__.tagIcon == 'custom') {
        this.$emit("func", data, model, value ,codetype, 1);
      } else {
        this.$emit("func", data, model, value ,codetype, 2);
      }
    },
    doSomethingother(data, model, value,codetype) {
      this.$emit("funcOther", data, model, value ,codetype);
    },
    setcon(aValue, bValue, value ,codetype) {
      let config = codetype
      var that = this;
      if (codetype.hasOwnProperty('fun')) {
        var func = config.fun;
        var dom = new Function(`return ${func}`)();
        dom(value, this, config)
        // console.log(value)
        // console.log(this.DateDiff(value.m[0],value.m[1]))
        // console.log(codetype)
        // this.formConfCopy.fields.forEach(el => {
        //     if (el.__vModel__ === config.result) {
        //         this.$set(el.__config__, "defaultValue", dom(value));
        //         this.formData[config.result] = dom(value)
        //     }
        // });
      }
    },
    initFormData(componentList, formData) {
      componentList.forEach(cur => {
        const config = cur.__config__
        if (cur.__vModel__) formData[cur.__vModel__] = config.defaultValue
        if (config.children) this.initFormData(config.children, formData)
      })
    },
    buildRules(componentList, rules) {
      componentList.forEach(cur => {
        const config = cur.__config__
        if (Array.isArray(config.regList)) {
          if (config.required) {
            const required = { required: config.required, message: cur.placeholder }
            if (Array.isArray(config.defaultValue)) {
              required.type = 'array'
              required.message = `${config.label}`
            }
            required.message === undefined && (required.message = `${config.label}`)
            config.regList.push(required)
          }
          rules[cur.__vModel__] = config.regList.map(item => {
            item.pattern && (item.pattern = eval(item.pattern))
            item.trigger = ruleTrigger && ruleTrigger[config.tag]
            return item
          })
        }
        if (config.children) this.buildRules(config.children, rules)
      })
    },
    resetForm() {
      this.formConfCopy = deepClone(this.formConf)
      this.$refs[this.formConf.formRef].resetFields()
    },
    submitForm() {
      this.$refs[this.formConf.formRef].validate(valid => {
        if (!valid) return false
        // sumit
        this.$emit('submit', this[this.formConf.formModel])
        return true
      })
    }
  },
  render(h) {
    return renderFrom.call(this, h)
  }
}
</script>